import React from 'react';
import s from './Card.module.css';

function Card(props) {
  // ac치 va tu c칩digo
    let icon = props.img;
    let url = `http://openweathermap.org/img/wn/${icon}@2x.png`;

  return (
      <div className={s.card}>
        <div className={s.btnCloseContainer}>
          <div  className={s.btnClose} onClick={props.onClose}>
            X
          </div>
        </div>
        <p className={s.nameCity}>{props.name}</p>
        <p className={s.country}>{props.country}</p>
        <p className={s.description}>{props.description}</p>
        <p className={s.actualText}>Actual</p>
        <p className={s.tempActual}>{props.temp}</p>
        <div className={s.temperatures}>
          <div>
            <p className={s.textMin}>Min</p>
            <p>{props.min}</p>
          </div>
          <div>
            <p className={s.textMax}>Max</p>
            <p>{props.max}</p>
          </div>
          <div>
            <img alt="climate" src={url} />
          </div>
        </div>

      </div>
  );


};

export default Card;