import React, { useState } from 'react';
import './App.css';
import Nav from './components/Nav.jsx';
import Cards from './components/Cards.jsx';

function App() {

  const [cities, setCities] = useState([]);

  function onSearch(ciudad){

    const { REACT_APP_API_KEY } = process.env;
    let url = `https://api.openweathermap.org/data/2.5/weather?q=${ciudad}&appid=${REACT_APP_API_KEY}&units=metric`;

    console.log(url);
    fetch(url)
      .then( respuesta => respuesta.json())
      .then( dataCiudadApi => {

        if(dataCiudadApi.main !== undefined){
          const nuevaCiudad = {
            name: dataCiudadApi.name,
            description: dataCiudadApi.weather[0].description,
            img: dataCiudadApi.weather[0].icon,
            temp: Math.round(dataCiudadApi.main.temp),
            min: Math.round(dataCiudadApi.main.temp_min),
            max: Math.round(dataCiudadApi.main.temp_max),
            country: dataCiudadApi.sys.country,
            id: Number(new Date())
          }
          setCities(oldCities => [...oldCities, nuevaCiudad]);
        }else {
          alert("Ciudad no encontrada");
        }
    })
  }

  function onClose(id) {
      setCities(oldCities => oldCities.filter(c => c.id !== id));
  }


  return (
    <div className="App">
      <div>
      	<Nav 
          onSearch={onSearch}
          onClose={onClose}
        />
      </div>
      <div>
      	<Cards 
      		cities={cities}
          onClose={onClose}
      	/>
      </div>
      
    </div>
  );

}
export default App;
